@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600;700&display=swap);

*{
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  padding: 0;
}
 

body {
  overflow-x: hidden;
  margin: 0;
  box-sizing: border-box;
  padding: 0;
  width:100%;
}



.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #28c76f;
    text-align: center;
    z-index: 1;
    position: -webkit-sticky !important;
    position: sticky !important;
    padding: 10px 8%;
    text-align: center;
    width: 100%;
}

.logo {
    width: 150px;
}

.nav-menu {
    display: flex;
    justify-content: space-between;
    align-items: center !important;
    position: relative;
    right: 0;
    text-align: center;
    margin-top: auto;
    margin-bottom: auto;
}

.nav-link {
    text-decoration: none;
    font-size: 12px !important;
    color: #fff !important;
    font-weight: bold;
    text-align: center;
    margin-left: 5px;
    padding: 5px !important;
}

.nav-link:hover {
   border-bottom: 2px solid #f1f1f1;
}


.hamburger {
    display: none;
}

.bar {
    display: block;
    width: 30px;
    height: 2px;
    margin: 5px auto;
    transition: all 0.3s ease-in-out;
    background-color: #ffffff;
    border-radius: 10px;
}



@media only screen and (max-width: 990px) {

    .navbar {
        padding: 10px 10px;
        width: 100% !important;
    }

    .nav-menu {
        position: fixed;
        left: 0%;
        top: 4rem;
        flex-direction: column;
        background-color: rgb(83, 160, 80);
        width: 100%;
        text-align: left;
        transition: 0.3s;
        box-shadow: 0 10px 27px rgba(0, 0, 0, 0.05);
        display: none;
        z-index: 1;
    }

    .active-menu {
        display: block;
    }

    .nav-menu.active {
        left: 0;
    }

    .nav-item {
        margin: 2.5rem 0;
    }

    .hamburger {
        display: block;
        cursor: pointer;
        
    }

    .hamburger.active .bar:nth-child(2) {
        opacity: 0;
    }

    .hamburger.active .bar:nth-child(1) {
        -webkit-transform: translateY(8px) rotate(45deg);
                transform: translateY(8px) rotate(45deg);
    }

    .hamburger.active .bar:nth-child(3) {
        -webkit-transform: translateY(-8px) rotate(-45deg);
                transform: translateY(-8px) rotate(-45deg);
    }


}

.nav-link-active {
    color: #00FF00;
}
.hero-page {
    min-height: 92vh;
    width: 100vw;
    display: flex;
    align-items: center;
    background-color: #28c76f;
    justify-content: center;
    border-top: 1px solid rgb(247, 247, 247);
    padding: 20px 8%;
}

.hero {
    display: flex !important;
    justify-content: space-around;
    margin: auto;

}

.right {
    margin: auto;
    width: 60%;
    padding-right: 20px;
}


.home-logo {
    max-width: 400px;
    display: block;
    margin: auto !important;
    position: relative;
    top: 15% !important;
}


.intro {
    box-shadow: rgba(0, 0, 0, 0.04) 0px 3px 5px;
    color: rgb(255, 255, 255);
    font-size: 40px;
    font-weight: bold;
}

.description-p {
    font-size: 16px;
    color: #fff;
    font-weight: 600;

}

.home-btns {
    display: flex;
    flex-wrap: wrap;
}

.get-started-home-button {
    background-color: rgb(255, 255, 255);
    text-decoration: none;
    padding: 10px 80px;
    color: rgb(73, 73, 73);
    border-radius: 10px;
    margin-bottom: 10px;
    font-size: 12px;
    font-weight: bold;
    margin-right: 20px;
}

.get-started-home-button:hover {
    background-color: transparent;
    color: #fff;
    border: 1px solid #fff;
}

.live-demo-home-button {
    background-color: transparent;
    text-decoration: none;
    padding: 10px 85px;
    color: #fff;
    border: 1px solid #fff;
    border-radius: 10px;
    margin-bottom: 10px;
    font-size: 12px;
    font-weight: bold;
}

.live-demo-home-button:hover {
    background-color: rgb(255, 255, 255);
    color: rgb(73, 73, 73);
}

.home-link:hover {
    background-color: rgb(25, 44, 24);
    border: 1px solid rgb(78, 161, 75);
    color: #fff;
}

#clients-section {
    width: 100vw;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
    grid-gap: 20px;
    min-height: 250px;
    align-items: center;
    padding: 0px 8%;
    background-color: #f7f7f7;
}

.client-logo {
    width: 120px;
}

#achievements-hero-section {
    min-width: 100vw;
    padding: 6% 8%;
}

.achievement-description {
    align-items: center;
    text-align: center;
    padding-bottom: 4%;
}

.achievement-header {
    font-size: 30px;
    font-weight: bold;
    color: #28c76f;
}

.achievement-p {
    padding: 20px 20%;
    font-size: 16px;
}

.achievements-section {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    grid-gap: 20px;
    padding-top: 5%;
    align-items: center;
    border-top: 3px solid #9ce9bd;
}

.achievement-item {
    text-align: center;
}

.achievement-value {
    font-size: 25px;
    font-weight: bold;
    color: #28c76f;
}

#why-us-section {
    min-width: 100vw;
    padding: 6% 15%;
    background-color: #f7f7f7;
}

.feature-description {
    align-items: center;
    text-align: center;
    padding-bottom: 4%;
}

.feature-header {
    font-size: 30px;
    font-weight: bold;
}

.feature-p {
    padding: 20px 0%;
    font-size: 16px;
}

.feature-item {
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    background-color: #fff;
    display: flex;
    justify-content: space-between;
    border-radius: 10px;
    align-items: center;
    margin-top: 20px;
    padding: 4% 5%;
}

.feature-logo {
    width: 50%;
}

.feature-h {
    font-size: 25px;
    font-weight: bold;
}


#testimonials-section {
    min-width: 100vw;
    padding: 6% 8%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-gap: 20px;
}

.testimonial-item {
    padding: 20px;
}

.testimonial {
    font-size: 14px;
}

.testimonial-item div {
    display: flex;
    align-items: center;
}

.client-image {
    width: 80px;
    height: 80px;
    border: 1px solid black;
    border-radius: 100% 100%;
    object-fit: contain;
    padding: 5px;
    margin-right: 20px;
}

.client-details {
    font-size: 12px;
}

@media only screen and (max-width: 990px) {

    .hero-page {
        width: 100% !important;
    }

    .hero {
        flex-direction: column;
        padding: 30px 0%;
        width: 100vw;
    }

    .right {
        width: 90vw;
        padding: 0px;
    }

    
    .home-logo {
        display: none;
    }


    #clients-section {
        padding: 0px 20px;  
    }


    #achievements-hero-section {
        min-width: 100vw;
        padding: 6% 10px;
    }

    .achievement-p {
        padding: 20px 0%;
    }

    #why-us-section {
        padding: 6% 10px;
    }

    .feature-item{
        flex-direction: column-reverse;
        align-items: flex-start;
    }

    #testimonials-section {
        padding: 6% 10px;
    }


}
.products-page {
    min-width: 100vw;
    min-height: 40vw;
    padding: 2% 15%;
}

.product-item {
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    background-color: #fff;
    display: flex;
    justify-content: space-between;
    border-radius: 10px;
    align-items: center;
    margin-top: 20px;
    padding: 2% 5%;
}

.product-logo {
    width: 35%;
}

.product-item div {
    margin: 0px 20px;
}

.product-title {
    font-size: 25px;
    font-weight: bold;
}


@media only screen and (max-width: 990px) {

    .products-page {
        padding: 2% 30px;
    }

    .product-item {
        padding: 2% 20px;
    }
 
}


@media only screen and (max-width: 770px) {

    .products-page {
        padding: 2% 10px;
    }

    .product-item {
        flex-direction: column-reverse;
        flex-wrap: wrap;
        justify-content: flex-start;
        padding: 5% 10px;
    }

    .product-logo {
        width: 100%;
    }

    .product-item div {
        margin: 0px 0px;
    }

    .product-description{
        margin-bottom: 20px;
    }
 
}
.contact-page {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    align-items: center;
    background-color: #f7f7f7;
    min-height: 82vh;
    padding: 5% 10%;
}

.contact-header h3{
  font-size: 30px;
  font-weight: bold;
}

.create-modal-inactive {
    display: none; 
    -webkit-animation: hide-modal .7s ease-in-out; 
            animation: hide-modal .7s ease-in-out;
}

.create-modal-active {
    display: block;
    position: fixed;
    z-index: 1000;
    left: 0;
    top: 0px;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgb(0, 0, 0);
    background-color: rgba(0, 0, 0, 0.4);
}


.contact-form {
    width: 450px;
    height: auto;
    border-radius: 5px;
    align-self: center;
    padding: 20px;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    background-color: #fff;
    display: block;
    margin: auto;
    margin-top: 20px;
}

.contact-page div{
    width: 50%;
}

.contact-card{
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
    background-color: #fff;
    display: flex;
    padding: 20px 20px;
    margin-bottom: 20px;
    border-radius: 10px;
    width: 100% !important;
    align-items: center;
    cursor: pointer;
}

.contact-card span{
    font-size: 16px;
    font-weight: bold;
}

#contact-icon{
    background-color: rgb(235, 245, 245);
    border-radius: 100%;
    padding: 10px;
    margin-right: 20px;
    text-align: center !important;
}


.form-control {
    border: 1px solid rgb(78, 161, 75) !important;
    font-size: 13px !important;
}

.form-control:focus {
    outline-style: none !important;
    box-shadow: none !important;
}

.form-group {
    width: 100%;
    margin: 20px auto;
}

label {
    font-size: 12px;
}

label span {
    font-size: 12px;
    opacity: 0.5;
}


.form-btns {
    display: flex;
    justify-content: space-between;
}

.submit-btn {
    background-color: rgb(78, 161, 75);
    text-align: center;
    border: none;
    font-size: 14px;
    color: #fff;
    text-transform: uppercase;
    padding: 5px 10px;
    margin-top: 20px;
    border-radius: 4px;
    min-width: 50%;
    margin-right: 10px;
}


.exit-btn {
    text-align: center;
    border: none;
    font-size: 14px;
    color: rgb(95, 95, 95);
    text-transform: uppercase;
    min-width: 50%;
    margin-top: 20px;
    border-radius: 4px;
    padding: 5px 10px;
}



@media only screen and (max-width: 770px) {

    .contact-page {
        padding: 5% 10px;
        flex-direction: column;
    }

    .contact-page div{
        width: 100%;
    }

    .contact-form {
        width: 95vw;
        margin-top: 30px;
    }

    .form-btns {
        flex-direction: column;
    }



}
.about-page {
    min-height: 90vh;
   
}

.about-left,
.about-right {
    padding: 2% 8%;
}

.about-left {
    text-align: center;
   
}

.about-right {
    background-color: #f7f7f7;
    text-align: center;
    width: 100vw;
}


.slogan-title,
.team-title,
.right-title {
    color: rgb(78, 161, 75);
    font-weight: bold;
    font-size: 30px;
}

.team-cards {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    grid-gap: 20px;
    margin-top: 30px;
}

#card-item {
    border: none;
}

.team-image {
    border: 1px rgb(104, 100, 100) solid;
    border-radius: 100% 100%;
    height: 150px;
    width: 150px;
    margin-right: 0px;
    object-fit: contain;
    padding: 5px;
}

.team-info {
    font-size: 13px;
}


.partners-cards {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    grid-gap: 20px;
    margin-top: 30px;
}

.partner-img {
    border: none;
    height: 300px;
    width: 200px;
    display: block;
    margin-left: auto;
    margin-right: auto;
    background-color: #fff;
    object-fit: contain;
    border-radius: 5px;
    padding: 5px;

}

.partner-info {
    font-size: 13px;
    text-align: center;
}





@media only screen and (max-width: 800px) {

    .about-page {
        flex-wrap: wrap;
        flex-direction: column;
        height: auto;
    }

    .about-left,
    .about-right {
        width: 100vw;
    }

}
.footer {
    background-color: #28c76f;
    position: relative;
    bottom: 0% !important;
    width: 100vw;
    border-radius: 3px;
    padding: 30px 8%;
    color: rgb(235, 228, 228);
    font-size: 16px;
    text-align: center;
}
